import React, { Component } from 'react';
import { connect } from 'react-redux';
import CircularProgress from '@material-ui/core/CircularProgress';
import ReactCrop from 'react-image-crop';
import 'tachyons';
import './styles.css';

import AddAPhotoOutlinedIcon from '@material-ui/icons/AddAPhotoOutlined';
// import 'react-image-crop/lib/ReactCrop.scss';
import 'react-image-crop/dist/ReactCrop.css';
import { Icon } from '@material-ui/core';

class ProfilePIc extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tookInput: true,
      crop: {
        aspect: 1 / 1,
        width: 20,
      },
      cropped: false,
      img: '',
      imgBlob: {},
      imgproperties: {},
    };
  }

  onChange = (crop) => {
    //   console.log(crop)
    this.setState({ crop });
  };

  componentWillMount() {
    const { photo, changProfile } = this.props;
    if (changProfile) {
      console.log(photo);
      this.setState({ tookInput: false, img: photo, changProfilePic: true });
    } else {
      console.log(photo);

      this.setState({ tookInput: true, img: photo, changProfilePic: false });
    }
  }

  componentWillReceiveProps(np) {
    const { photo, changProfile } = np;
    if (changProfile) {
      console.log(photo,changProfile);
      this.setState({ tookInput: false, img: photo, changProfilePic: true });
    } else {
      console.log(photo,changProfile);

      this.setState({ tookInput: false, img: photo, changProfilePic: false });
    }
  }


  getBase64(file, cb) {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = function () {
      cb(reader.result);
    };
    reader.onerror = function (error) {
      console.log('Error: ', error);
    };
  }

  getCroppedImg(image, crop, fileName) {
    const i = document.createElement('img');
    i.src = image;
    document.getElementById('body').appendChild(i);
    const canvas = document.createElement('canvas');
    const scaleX = i.naturalWidth / i.width;
    const scaleY = i.naturalHeight / i.height;
    canvas.width = crop.width;
    canvas.height = crop.height;
    const ctx = canvas.getContext('2d');
    // console.log(scaleX,scaleY)
    ctx.drawImage(
      i,
      crop.x * scaleX,
      crop.y * scaleY,
      crop.width * scaleX,
      crop.height * scaleY,
      0,
      0,
      crop.width,
      crop.height,
    );

    i.style.display = 'none';
    // document.getElementById('body').appendChild(canvas)
    const {
      name, lastModified, lastModifiedDate, webkitRelativePath, size, type,
    } = this.state.imgproperties;
    const base64Image = canvas.toDataURL('image/png');

    canvas.toBlob((blob) => {
      blob.name = name;
      blob.lastModified = lastModified;
      blob.lastModifiedDate = lastModifiedDate;
      blob.webkitRelativePath = webkitRelativePath;
      this.setState({
        img: base64Image,
        imgBlob: blob,
        crop: {
          aspect: 1 / 1,
          width: 20,
        },
        cropped: true,
      });
      console.log(blob);
    }, type, 1);


    // this.props.selectProfilePic(this.props.img)
  }

  selectProfilePic(pic) {
    if (pic === null) {
      this.setState({ img: null, tookInput: true, cropped: true });
    } else {
      this.getBase64(pic[0], (res) => this.setState({ img: res, tookInput: true, cropped: false }));
      this.setState({ imgproperties: pic[0] });
    }
  }

  render() {
    return (
      <div>
        {
          !this.state.changProfilePic
            ? (
              <div className="profile-img-div mt4 mb2 center">
                <div className="center"><img src={this.state.img} id="a" className="profile-img" /></div>
              </div>
            )
            : (
              <div>
                <div className="profile-img-div mt4 mb2 center">
                  <div className="center">
                    <input
                      type="file"
                      id="avatar"
                      name="avatar"
                      accept="image/png, image/jpeg"
                      className="img-input"
                      onChange={(e) => this.selectProfilePic(e.target.files)}
                    />
                    {
                      this.state.img ?<img src={this.state.img} id="a" className="profile-img" alt='profile-img' /> :
                      <AddAPhotoOutlinedIcon className="profile-img" />
                    }
                    
                    
                  </div>

                  
                </div>
                <div className="tc" style={{ alignContent: 'center', alignItems: 'center' }}>
                        <button type="button" onClick={() => this.selectProfilePic(null)} className="profile-btn white center mt0 pt0 mb0"> Remove Picture</button>
                      </div>
                {/* {
                  this.state.tookInput
                    ? (
                      
                    )
                    : null
                } */}
                {
                  (!this.state.cropped && this.state.tookInput)
                    ? (
                      <div className="tc" style={{ alignContent: 'center', alignItems: 'center' }}>
                        <ReactCrop src={this.state.img} crop={this.state.crop} onChange={this.onChange} />

                        <button type="button" onClick={() => this.getCroppedImg(this.state.img, this.state.crop, 'abc')} className="color-btn  center mt0 pt0 mb0"> Crop Picture</button>
                      </div>
                    ) : null
                }
                {
                    (this.state.cropped && this.state.tookInput)
                      ? <button type="button" onClick={() => this.props.selectProfilePic(this.state.imgBlob)} className="color-btn  center mt0 pt0 mb0"> Upload Picture</button>
                      : null
                }
              </div>
            )
        }


      </div>
    );
  }
}


const mapStateToProps = (state) => ({
});

const mapDispatchToProps = (dispatch) => ({
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(ProfilePIc);
